.NewsArticlesList{
    border: none;

    h4{
        color: var(--font-color-primary);
        font-size: 1.5rem;
        padding: 1rem;
        text-align: right;
    }
}
.articles-list{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.articles-list-item{
    padding: 1rem;
    border-radius: 50px;
    background: var(--btn-color-primary);
    
    h5{
        color: var(--font-color-primary);
        font-size: 1.2rem;
        text-transform: uppercase;
    }
    p{
        color: var(--font-color-primary);
        font-size: .8rem;
    }
}