

.HomeHero{
    background: var(--bg-color-primary);
    color: var(--font-color-primary);
    padding: 1.5rem;
    
    h2{
        font-size: 1.5rem;
        text-transform: uppercase;
    }
    h3{
        font-size: 3rem;
        padding-bottom: 1rem;
        text-transform: uppercase;
    }
    p{
        font-size: 1rem;
        padding-bottom: 1rem;
    }
}
.home-hero-button::after{
    content: "Events";
    position: absolute;
    top: 100%; left: 50%;
    transform: translateX(-50%);
}