.PhotoViewer{
    position: fixed;
    top: 0; left: 0;
    width: 100vw;
    height: 100vh;
    display: none;
    z-index: 3;
}
.PhotoViewer.open{
    display: block;
    overflow: scroll;
}
.PhotoViewer-overlay{
    width: 100vw;
    height: 100vh;
    background-color: hsl(0 0% 0% / .7);
    display: flex;
    align-items: center;
    z-index: 4;
    animation: fadeIn 250ms linear forwards;
}
.PhotoViewer-photo-container{
    width: 100vw;
    img{
        width: 100vw;
    }
}