:root{
  --font-color-primary: #fff;
  --body-color: hsl(220 13% 18%);
  --bg-color-primary: hsl(0 0% 0% / .2);
  //--btn-color-primary: hsl(120 100% 30% / .3);
  --btn-color-primary: linear-gradient( hsl(120 100% 50% / .5), hsl(120 100% 40% / .3) 15%, hsl(120 100% 40% / .3) 80%, hsl(120 100% 50% / .5));
  --btn-active-color: hsl(120 100% 40% / .3);
  --icon-color: hsl(120 100% 50% / .7);
}

*{margin: 0; padding: 0;box-sizing: border-box;}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p{
  line-height: 1.5rem;
}

/*  UTIILITY  */

.stopScroll{
  overflow: hidden;
}

/*  COMPOSITION */

.page-title{
  position: relative;
  color: var(--font-color-primary);
  font-size: 1.2rem;
  font-weight: 500;
  text-align: left;
  padding: 1rem;
  z-index: 1;
  
  &::after{
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    font-size: 4rem;
    color: hsl(0 0% 0% / .2);
    text-align: left;
    z-index: -1;
  }
}

/*  ANIMATIONS  */

@keyframes slideContentUp {
  from{
      opacity: 0;
      transform: translateY(100px);
  }
  to{
      opacity: 1;
      transform: translateY(0px);
  }
}
@keyframes fadeIn {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}
