
.NextEvent{
    background: var(--bg-color-primary);
    padding: 1rem 1rem 2rem 1rem;
    color: var(--font-color-primary);

    .next-event-date{
        font-weight: 700;
    }
    .next-event-time{
        font-size: .9rem;
    }
    .next-event-description{
        line-height: 1.5rem;
        padding-bottom: 1rem;
    }
    
}
.next-event-title-container{
    padding: 1rem;

    h2{
        line-height: 2rem;
    }
}
.next-event-button::after{
    content: "Details";
    position: absolute;
    top: 100%; left: 50%;
    transform: translateX(-50%);
}
