
.UpcomingEvents{
    background: none;

    h2{
        color: var(--font-color-primary);
        padding: 1rem;
    }
}
.upcoming-events-list{
    list-style: none;
    color: var(--font-color-primary);
    display: grid;
    gap: 1rem;

    li{
        background: var(--btn-color-primary);
        padding: 1rem;
        border-radius: 50px;
        cursor: pointer;

        h4{
            text-transform: uppercase;
        }
        &:active{
            transform: scale(.95);
        }
    }

    
}