.ArticleViewer{
    display: none; // on/off control
    z-index: 4;
    position: fixed;
    top: 0; left: 0;
    width: 100vw;
    height: 100vh;
    overflow: scroll;

    .ArticleViewerCloseButton{
        position: absolute;
        top: 3vh; right: 5vw;
        z-index: 5;

        &::after{
            content: attr(datatitle);
            position: absolute;
            top: 100%; left: 50%;
            transform: translateX(-50%);
        }
    }
}

.ArticleViewer.open{
    display: block;
}
.ArticleViewer-content-wrapper{
    width: 100%;
    padding: 5rem 1rem 2rem 1rem;
    background-color: var(--body-color);
    opacity: 0;
    transform: translateY(100px);
    animation: slideContentUp 300ms linear forwards;

    .ArticleViewer-title{
        color: var(--font-color-primary);
        font-size: 2rem;
    }
    .ArticleViewer-date{
        color: var(--font-color-primary);
        padding-bottom: 2rem;
    }
    .ArticleViewer-paragraph{
        color: var(--font-color-primary);
        padding-bottom: 1rem;
    }
    .ArticleViewer-author{
        color: var(--font-color-primary);
        padding: 1rem;
        text-align: left;
    }
}
