
.FeaturedVideoSection{
    border: none;
    background-image:
    linear-gradient(to bottom, hsl(0 100% 100% / .3), hsl(0 0% 0% / .5)),
    url('../../../../img/two-riders.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    padding: 1rem 0;
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;

    h3{
        color: var(--font-color-primary);
        text-align: left;
        margin-left: 1rem;
        text-shadow: 2px 2px 2px hsl(0 0% 0% / .9);
    }
    p{
        width: 90%;
        color: var(--font-color-primary);
        font-size: .9rem;
        margin: 0 auto;
    }
}
.feature-button-img-container{
    background: hsl(0 0% 0% / .3);
    padding: 1rem 0;

    .play-circle-icon{
        cursor: pointer;
    }
}