
.ContactForm{
    position: relative;
    background: var(--bg-color-primary);
    border-radius: 5px;
    padding: 1rem;
    display: grid;
    grid-template-columns: 1fr;
    gap: .5rem;

    h3{
        color: var(--font-color-primary);
    }
    label{
        display: block;
        text-align: left;
        color: var(--font-color-primary);
        font-size: .9rem;
        font-weight: 500;
        text-transform: uppercase;
        transition: transform 100ms ease;
    }
    input{
        width: 100%;
        border: none;
        font-size: 1.2rem;
        line-height: 1.5rem;
        font-family: Arial, Helvetica, sans-serif;
        background: hsl(0 100% 100% / .1);
        color: var(--font-color-primary);
        padding: .5rem;
    }
    textarea{
        color: var(--font-color-primary);
        width: 100%;
        font-size: 1.2rem;
        font-family: Arial, Helvetica, sans-serif;
        background: hsl(0 100% 100% / .1);
        border: none;
        border-bottom: 1px solid hsl(0 100% 100% / .1);
        padding: .5rem;
    }
    
    button{
        color: var(--font-color-primary);
        width: 100%;
        background: none;
        background-color: var(--btn-color-primary);
        border: none;
        border-radius: 50px;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 1.2rem;
        padding: .5rem 0;
        cursor: pointer;

        &:active{
            background: hsl(0 0% 0% / .3);
        }
    }
}