
.VideoPlayerComponent{
    color: #fff;
    position: fixed;
    top: 0; left: 0;
    width: 100vw;
    max-height: 100vh;
    background: #000;
    z-index: 3;
    display: none;
    grid-template-columns: 1fr;
    overflow-y: scroll;
}
.VideoPlayerComponent.open{
    display: block;
}

.VideoPlayerComponentCloseButton{
    position: fixed;
    bottom: 5vh; right: 5vw;
}
.VideoPlayerComponentCloseButton::after{
    content: attr(datatitle);
    position: absolute;
    top: 110%; left: 50%;
    transform: translateX(-50%);
    text-transform: uppercase;
    font-weight: 900;
}
.VideoPlayer-container{
    border: none;
    max-width: 500px;
}
.VideoPlayer-display{
    aspect-ratio: 4/3;
    overflow: hidden;
    display: block;
}
.VideoPlayer-display-information{
    display: grid;
    grid-template-columns: 1fr;
    padding-bottom: 2rem;
    
    h4{
        color: #fff;
        padding: .5rem 1rem;
        text-align: left;
    }
    .VideoPlayer-video-date{
        font-size: .8rem;
        text-align: left;
        padding: .5rem 1rem;
    }
    button{
        color: var(--font-color-primary);
        background: hsl(0 100% 100% / .1);
        border: none;
        text-align: right;
        padding: .5rem 1rem;
    }
    .VideoPlayer-video-description{
        font-size: .8rem;
        text-align: center;
        padding: .5rem 1rem;
        display: none;
    }
}
.VideoPlayer-VideoList-container{
    height: 50vh;
    padding: 1rem 0;
    background: hsl(0 100% 100% / .1);
    overflow-y: scroll;
}