
.NavigationPrimary{
    position: absolute;
    top: 0; left: 0;
    width: 100vw;
    //background-color: hsl(220 13% 18% / .5);
    background-color: hsl(0 0% 0% / .8);
    padding: 1rem 3rem 3rem 3rem;
    transform: translateY(-100%);
    z-index: 2;
    transition: all 200ms ease;

    h2{
        color: #fff;
        padding: 1rem;
        text-transform: uppercase;
        margin-bottom: 2rem;
    }

    ul{
        list-style: none;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    li{
        color: #fff;
    }
}
.NavigationPrimary.open{
    transform: translateY(0%);
}

.primary-navigation-link{
    color: #fff;
    background: radial-gradient(hsl(0 100% 100% / .5), hsl(0 0% 0%) 90%);
    font-size: 1.2rem;
    padding: 5px 0;
    display: inline-block;
    width: 100%;
    height: 100%;
    line-height: 1.5rem;
    text-decoration: none;
    border-bottom: 2px solid var(--icon-color);
    //border: 2px solid var(--icon-color);
    //box-shadow: 0px 0px 5px hsl(0 100% 100% / .2);
    text-shadow: 0px 0px 1px hsl(120 100% 50% / .3);
    text-transform: uppercase;
    font-weight: 700;
    border-radius: 50px;
    cursor: pointer;

    &:hover{
        background-color: hsl(0 100% 100% / .5);
    }
    &:active{
        transform: scale(.95);
    }
}