
.GearRequirements{
    background: var(--bg-color-primary);
    
    h3{
        padding: 1rem;
        color: var(--font-color-primary);
    }
 }
 .gear-content-container{
     padding: 1rem;
     border: none;
     display: grid;
     grid-template-columns: 1fr 1fr 1fr 1fr;
     grid-template-rows: 2;
     gap: 1rem;
 }
 
 .gear-container{
     border: none;
     
     img{
        filter: invert(80%) sepia(74%) saturate(1994%) hue-rotate(121deg) brightness(107%) contrast(100%);
        -webkit-filter: invert(80%) sepia(74%) saturate(1994%) hue-rotate(121deg) brightness(107%) contrast(100%);
     }
     p{
         font-size: .8rem;
         color: var(--font-color-primary);
     }
     sub{
         color: yellow;
     }
 }
 .gear-container:nth-child(5){
    grid-column: 1/3;
    grid-row: 2;
 }
 .gear-container:nth-child(6){
     grid-column: 2/4;
     grid-row: 2;
}
.gear-container:nth-child(7){
     grid-column: 3/5;
     grid-row: 2;
}
