
.Spinner{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: hsl(0 0% 0% / .9);
    display: none; // on and off
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
    overflow: hidden;
    z-index: 100;

    p{
        position: relative;
        font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        color: var(--font-color-primary);
        font-weight: 900;
        font-size: 2rem;
        color:hsl(0 100% 100% / .1);
    }
    p::after{
        content: "...";
        position: absolute;
        top: 0; left: 0;
        transform-origin: center;
        transform: rotate(180deg);
        color: var(--font-color-primary);
        animation: addingDots 1.5s ease-in-out infinite;
    }
}
.Spinner-animation-container{
    position: relative;
    width: 100%;
    min-height: 100px;

    .tire-container{
        position: absolute;
        top: 50%; left: 50%;
        animation: rotateTire 5s linear infinite;

        .tire{
            --tire-width: 20px;
            width: 40vw;
            height: 40vw;
            border-radius: 50%;
            background-color: transparent;
            
        }
        .stoke{
            position: absolute;
            top: 50%; left: 50%;
            width: 100%;
            height: 100%;
            animation: blinkStoke 1s linear infinite;
            font-size: 1.5rem;
        }
        .stoke1{
            transform: translate(-50%,-50%) rotate(0deg);
        }
        .stoke2{
            transform: translate(-50%,-50%) rotate(45deg);
        }
        .stoke3{
            transform: translate(-50%,-50%) rotate(90deg);
        }
        .stoke4{
            transform: translate(-50%,-50%) rotate(135deg);
        }
        .stoke5{
            transform: translate(-50%,-50%) rotate(180deg);
        }
        .stoke6{
            transform: translate(-50%,-50%) rotate(225deg);
        }
        .stoke7{
            transform: translate(-50%,-50%) rotate(270deg);
        }
        .stoke8{
            transform: translate(-50%,-50%) rotate(315deg);
        }
    }
    
    .road-container{
        position: absolute;
        bottom: 0; left: 0;
        width: 100%;
        height: 33%;
        border: 1px solid limegreen;
    }
}
@keyframes blinkText {
    from{
        color: hsl(0 100% 100% / .1);
    }
    to{
        color: hsl(0 100% 100% / 1)
    }
}
@keyframes rotateTire {
    0%{
        transform: translate(-50%,-50%) rotate(0deg);
    }
    50%{
        transform: translate(-50%,-50%) rotate(180deg);
    }
    100%{
        transform: translate(-50%,-50%) rotate(360deg);
    }
}
@keyframes blinkStoke {
    from{
        color: hsl(0 100% 100% / .1);
    }
    to{
        color: hsl(0 100% 100% / 1)
    }
}
@keyframes addingDots {
    0%{
        content: ".";
    }
    25%{
        content: "..";
    }
    50%{
        content: "...";
    }
    75%{
        content: "....";
    }
    100%{
        content: ".....";
    }
}