.AddressSection{
    color: var(--font-color-primary);
    background: var(--bg-color-primary);
    padding: 1rem;
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    justify-content: center;
    gap: 1rem;

    .location-icon{
        margin: 0 auto;
    }
    .address{
        display: block;
        text-decoration: none;
        line-height: 1.5rem;
        color: var(--font-color-primary);
        background-color: var(--btn-color-primary);
        border-radius: 5px;
        padding:.25rem .5rem;
    }
}