.InformationSection{
    display: grid;
    gap: 1rem;

    article{
        background: var(--bg-color-primary);
        padding: 1rem;

        h2{
            color: var(--font-color-primary);
            line-height: 1;
            margin-bottom: .5rem;
            border-radius: 5px;
        }
        p{
            color: var(--font-color-primary);
        }
        em{
            color: var(--font-color-primary);
            font-size: .8rem;
        }
    }
    > p{
        color: var(--font-color-primary);
        background: var(--bg-color-primary);
        padding: 1rem;
        border-radius: 5px;
    }
}
.Information-hours p:first-of-type{
    margin-bottom: .5rem;
}
.event-link{
    color: var(--font-color-primary);
    background: var(--btn-color-primary);
    padding: .25rem;
    border-radius: 5px;
    text-decoration: none;
    font-size: 1rem;
    font-weight: 700;
}