.EventViewer{
    display: none; // on/off control
    z-index: 4;
    position: fixed;
    top: 0; left: 0;
    width: 100vw;
    height: 100vh;
    overflow: scroll;

    .EventViewerCloseButton{
        position: absolute;
        top: 3vh; right: 5vw;
        z-index: 5;

        &::after{
            content: attr(datatitle);
            position: absolute;
            top: 100%; left: 50%;
            transform: translateX(-50%);
        }
    }
}
.EventViewer.open{
    display: block;
}
.EventViewer-content-wrapper{
    width: 100%;
    padding: 5rem 1rem 2rem 1rem;
    background-color: var(--body-color);
    display: grid;
    gap: 1rem;
    opacity: 0;
    transform: translateY(100px);
    animation: slideContentUp 300ms linear forwards;

    h2{
        color: var(--font-color-primary);
        font-size: 3rem;
    }
    p{
        color: var(--font-color-primary);
    } 
    img{
        width: 100%;
    }
}
.EventViewer-date-container{
    display: grid;
    text-transform: uppercase;
    padding: 1rem;
    border-radius: 5px;
    background-color: var(--bg-color-primary);

    p:nth-child(1){
        font-size: 3rem;
        font-weight: 900;
        padding: 1rem;
    }
    p:nth-child(2){
        font-size: 2rem;
        font-weight: 700;
    }
    p:nth-child(3){
        font-size: .8rem;
    }
}
.EventViewer-content-button-container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    gap: .5rem;
}
.EventViewer-link{
    color: var(--font-color-primary);
    background-color: var(--bg-color-primary);
    text-decoration: none;
    font-size: .8rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: .25rem;
}