.CityDirections{
    border: none;
}
.city-directions-header{
    background: hsl(0 0% 0% / .2);
    border-radius: 50px;
    display: grid;
    grid-template-columns: 2fr 1fr;
    overflow: hidden;
    
}
.city-directions-title-container{
    padding: 1rem .5rem;
}
.directions-details-button{
    position: relative;
    background: none;
    background: var(--btn-color-primary);
    color: var(--font-color-primary);
    border: none;
    padding: 5px;
    cursor: pointer;

    &:active{
        transform: scale(.5rem);
        background-color: var(--btn-active-color);
    }
}
.city-direction-details-container{
    display: none;
}
.city-direction-details-container.open{
    display: block;
}
.city-direction-details{
    background: hsl(0 0% 0% / .2);
    border-radius: 10px;
    padding: 1rem;
    width: 90%;
    max-width: 40rem;
	text-align: left;
    margin: 0 auto;
	display: block;
    list-style: none;
    transform-origin: top;
    transform: scaleY(0);
}
.city-direction-details li{
    font-size: 1rem;
    line-height: 1.25rem;
    padding: .5rem 0;
    opacity: 0;
    transform: translateX(-10px);
    transition: all 1s ease 1s;
}

.city-direction-details.open{
    animation: slideDown 250ms linear forwards;
}
.city-direction-details.open li{
    animation: slideRight 250ms linear forwards 250ms;
}

@keyframes slideDown {
    from{
        transform: scaleY(0);
    }
    to{
        transform: scaleY(1);
    }
}
@keyframes slideRight {
    from{
        opacity: 0;
        transform: translateX(-10px);
    }
    to{
        opacity: 1;
        transform: translateX(0px);
    }
}