.AppFooter{
    padding: 1rem;
    border-top: 1px solid hsl(0 100% 100%);
    background: var(--bg-color-primary);
    display: grid;
    grid-template-columns: 1fr 1fr;
    color: var(--font-color-primary);

    .feedback-btn{
        border: 1px solid #fff;
        border-radius: 5px;
        background: none;
        color: var(--font-color-primary);
    }
}