.TopNews{
    background: var(--bg-color-primary);
    border-radius: 5px;
    padding: 1rem 1rem 2rem 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;

    .TopNews-date{
        width: 100%;
        text-align: left;
        font-size: .8rem;
    }

    h3{
        color: var(--font-color-primary);
        font-size: 2rem;
        text-transform: uppercase;
    }
    .TopNews-img-container{
        img{
            width: 100%;
            max-width: 500px;
        }
    }
    p{
        color: var(--font-color-primary);
        font-size: 1.2rem;
    }
}

.TopNews-button{
    position: relative;
    width: 100%;
    background: none;
    background-color: var(--btn-color-primary);
    border: none;
    padding: 1rem;
    border-radius: 50px;
    color: var(--font-color-primary);
    font-size: 1rem;
    font-weight: 700;
    text-transform: uppercase;
}