
.ContactLinks{
    padding: 1rem;
    background: var(--bg-color-primary);
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.ContactLink-container{
    img{
        width: 7vh;
        filter: grayscale(100%) contrast(700%) invert(100%);
    }
    p{
        color: var(--font-color-primary);
    }
}