
.VideoList{
    border: none;
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 1rem 0;
}
.video-block{
    box-shadow: -2px 0 5px hsl(0 0% 0% / .5);
    color: var(--font-color-primary);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

    img{
        width: 100%;
        grid-column: 1;
    }
}
.video-block-text-content{
    grid-column: 2/4;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;

    h4{
        border:none;
    }
    p{
       font-size: .8rem;
    }
    span{
        margin: 0 1rem;
    }
}