.ButtonPrimary{
    position: relative;
    background: none;
    border: none;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;

    &:active{
        transform: scale(.8);
        background-color: var(--bg-color-primary);
        box-shadow: 0px 0px 5px hsl(0 100% 100% / .5);
    }
    .FaFingerprint{
        color: var(--icon-color);

        &:active{
            color: hsl(120 100% 50% / .5);
        }
    }

}